.nav-wrapper {
    display: flex;
    width: 250px;
    height: 100vh;
    overflow-x: hidden;
    position: fixed;
    z-index: 10;
    flex-shrink: 0;
    background: var(--bg);
    // box-shadow: 6px 0px 18px #f1f1f1;
    box-shadow: 0px 0px 10px #f1f1f1;
    .side-bar {
        width: 100%;
        overflow-y: auto;
        .side-nav-header {
            display: flex;
            width: 100%;
            align-items: center;
            a {
                text-decoration: none;
                color: var(--primary-color);
            }
        }
        .sidebar-menu {
            .menu-item {
                margin: 10px auto;
                .menu-item-icon {
                    width: 20px;
                    flex-shrink: 0;
                    svg {
                        width: 100%;
                        height: 100%;
                    }
                    img {
                        width: 100%;
                    }
                }
                a {
                    text-decoration: none;
                    color: var(--text-color);
                    transition: 0.3s;
                    font-size: 1rem;
                    display: flex;
                    align-items: center;
                    &:hover {
                        color: var(--primary-color);
                    }
                }
                a.active {
                    color: var(--primary-color);
                    svg {
                        path {
                            stroke: var(--primary-color);
                        }
                        rect {
                            fill: var(--primary-color);
                        }
                    }
                }
            }
        }
    }
}
